<style lang="scss" scoped>
</style>

<template>
  <div class="ui">

    {{u}}
    <data-statis-one :icon="icon" title="" :data="statisData"></data-statis-one>
    <data-statis-card :data="cardData" style="margin-bottom:21px"></data-statis-card>

    <sub-title style="margin-bottom: 16px" title="(tce-eq)">
      <el-dropdown>
        <span class="el-dropdown-link">
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </sub-title>

    <custom-line
      style="margin-bottom: 29px; height: 190px"
      yAxisName=""
      showLegend="false"
      :colors="[{ color: '#00FFE0' }]"
      :source="lineData"
    ></custom-line>
  </div>
</template>

<script>
import {queryEnterpriseEnergyEfficiencyz} from "@/api/energy"
export default {
  data() {
    return {
      lineData: [],
      icon:require("../../../../../assets/energy/icon1.png")
    };
  },
  props: {
    time: String,
  },
  computed: {
    cardData(){
      return [
            {
          icon: require("../../../../../assets/overview/icon11.png"),
          name: "",
          value: 54508,
          unit1: "kWh",
          unit2: "MKWh",
        },
        {
          icon: require("../../../../../assets/overview/icon12.png"),
          name: "",
          value: 54508,
          unit1: "",
          unit2: "",
        },
        {
          icon: require("../../../../../assets/overview/icon13.png"),
          name: "",
          value: 54508,
          unit1: "m",
          unit2: "m",
        },
        {
          icon: require("../../../../../assets/overview/icon14.png"),
          name: "",
          value: 54508,
          unit1: "m",
          unit2: "m",
        },
      ]
    },
    statisData(){
      return[
        {
        value:1662974.13,
        unit1:"(/",
        unit1:"(/"
      }
      ]
    }
  },
  methods: {
    async loadStatisData(){
      queryEnterpriseEnergyEfficiencyz({}).then(res=>{
        console.log(res);
      })
    },  
    async loadLineData() {
      this.lineData = [
        {
          name: "10:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
        {
          name: "11:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
        {
          name: "12:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
        {
          name: "13:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
        {
          name: "14:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
        {
          name: "15:00",
          "tce-eq": Math.ceil(Math.random() * 100),
        },
      ];
    },
    async loadData() {
      this.loadLineData();
      this.loadStatisData()
    },
  },
  created() {
    this.loadData();
  },
  watch: {
    time() {
      this.loadData();
    },
  },
};
</script>